body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.stopped {
  color: rgb(0, 148, 25);
  text-align: center
} 
.running {
  color: red;
  text-align: center
} 
.panel-footer:hover {
  cursor: pointer;
}
.clockrunning {
  text-align: center;
  font-size: 26px;
}
.clockdescription {
  color: rgb(126, 126, 126);
}
.clockoptions {
  text-align: right;
  margin-right: 12px;
  margin-bottom: 10px
}
.title {
  text-align: center;
  margin-top: 25px;
  margin-bottom: 25px;
  border-bottom-color: rgb(156, 156, 156)
}
.hidden {
 visibility: hidden 
 }
.clickableGly {
  margin-left: 10px
}
.clickableGly:hover {
  cursor: pointer;
}
